
.footer {
  margin-top: 30px;
  text-align: center;
}
.footer .slogan {
    margin-bottom: 15px;
    letter-spacing: -3px;
    font-size: 0;
}
.footer .slogan img {
      display: inline-block;
      margin-right: 12px;
      vertical-align: middle;
}
.footer .slogan figcaption {
      display: inline-block;
      height: 48px;
      text-align: left;
      vertical-align: middle;
      letter-spacing: normal;
}
.footer .slogan figcaption strong {
        line-height: 28px;
        font-size: 24px;
}
.footer .slogan figcaption p {
        line-height: 20px;
        font-size: 14px;
}
.footer .link2ios a {
    color: #42bd56;
    font-size: 15px;
    text-decoration: none;
}



