
@import url(./assets/css/reset.css);
html,
body {
  height: 100%;
}
body {
  color: #111;
  font-family: "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
}
#app {
  padding-bottom: 30px;
}
#main {
  padding-top: 48px;
  max-width: 580px;
  margin: auto;
  overflow: hidden;
}
.line-spacing {
  line-height: 22px;
  word-wrap: break-word;
}
.inline-close {
  letter-spacing: -3px;
  font-size: 0;
}
.icon-box {
  display: inline-block;
  margin: 10px 15px 0 0;
  font-size: 12px;
  color: #d4d4d4;
}



